import logo from '~/static/images/starter-logo.svg'
import brand from '~/static/text/brand'

export default {
  data() {
    return {
      logo: logo,
      brand: brand
    }
  },
  head() {
    return {
      title: brand.starter.name + ' - Coming Soon'
    }
  },
  computed: {
    isDesktop() {
      const smUp = this.$store.state.breakpoints.smUp
      return smUp.indexOf(this.$mq) > -1
    }
  }
}
